<template>
  <div class="wrapper-bg">
  <div class="wrapper">
    <div class="w-seach" @click="handlePopShow">
      <p :title="curName.length>6?curName:''">{{curName}}</p>
    </div>
    <transition name="fade" class='sisizng'>
      <qvSearch v-if="popShow" :name="curName" @changepop="popClick"></qvSearch>
    </transition>
    <div class="content">
      
      <div class='w-center'>
        <ctitle :title="'一级内容分类TOP10'"></ctitle>
        <div class="c-title">Top10信访量(占比75%)</div>
        <div class="w-echart" ref="barEl">
          <PaimBar :params="zrdwBarYjParams"></PaimBar>
        </div>
      </div>
      <div class='w-center'>
        <ctitle :title="'二级内容分类TOP10'"></ctitle>
        <div class="c-title">Top10信访量(占比72%)</div>
        <div class="w-echart" ref="barEl">
          <PaimBar :params="zrdwBarEjParams"></PaimBar>
        </div>
      </div>
      <div class='w-center'>
        <ctitle :title="'三级内容分类TOP10'"></ctitle>
        <div class="c-title">Top10信访量(占比69%)</div>
        <div class="w-echart" ref="barEl">
          <PaimBar :params="zrdwBarSjParams"></PaimBar>
        </div>
      </div>
    </div>
  </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import ctitle from "@/components/title/index";
  import PaimBar from "@/components/bar/PaimBar1";
  import qvSearch from "@/components/depSeach/qvSearch";

  export default {
    name: "xfnrfl",
    components: {
      ctitle,
      PaimBar,
      qvSearch,
    },
    data() {
      return {
        curName: "广东省",
        erjiName: '',
        popShow: false,
        listScroll: null,
        // 1.区域受理量动态
        shij_02: '',
        xj_02: '',
        
        // 1.一级内容分类
        zrdwBarYjParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            click: true
          }
        },
        // 2.二级内容分类
        zrdwBarEjParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            click: true
          }
        },
        // 3.三级内容分类
        zrdwBarSjParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            click: true
          }
        },
      };
    },
    mounted() {
      this.getAPI(this.date);
    },
    methods: {
      handlePopShow() {
        this.popShow = true;
      },
      // 点击弹框确认按钮后触发
      popClick(obj) {
        this.shej_02 = !this.shej_02 ? Object.values(obj)[0] : '';
        this.shij_02 = !this.shij_02 ? Object.values(obj)[1] : '';
        this.xj_02 = !this.xj_02 ? Object.values(obj)[2] : '';
        this.getAPI(this.date);
        // this.curName = this.curName ?this.shej_02||this.shij_02||this.xj_02
        if (this.shej_02) {
          this.curName = this.shej_02
        } else if (this.shij_02) {
          this.curName = this.shij_02
        } else if (this.xj_02) {
          this.curName = this.xj_02
        }
      },

      // 2.一级二级三级内容分类
      getAPI(date) {
        get_http("xfnrflyj", {
          day: `[${date.startDate},${date.endDate}]`,
          Cqh:this.curName,
        })
        .then(res => {
          if (res.data.data.length) {
            this.zrdwBarYjParams.data = res.data.data.sort((a, b) => a.value - b.value);
          } else {
            this.zrdwBarYjParams.data = [];
          }
        })
        .catch(err => {
          console.log(err);
        });

        get_http("xfnrflej", {
          day: `[${date.startDate},${date.endDate}]`,
          Cqh:this.curName,
        })
        .then(res => {
          if (res.data.data.length) {
            this.zrdwBarEjParams.data = res.data.data.sort((a, b) => a.value - b.value);
          } else {
            this.zrdwBarEjParams.data = [];
          }
        })
        .catch(err => {
          console.log(err);
        });

        get_http("xfnrflsj", {
          day: `[${date.startDate},${date.endDate}]`,
          Cqh:this.curName,
        })
        .then(res => {
          if (res.data.data.length) {
            this.zrdwBarSjParams.data = res.data.data.sort((a, b) => a.value - b.value);
          } else {
            this.zrdwBarSjParams.data = [];
          }
        })
        .catch(err => {
          console.log(err);
        });
      }
    },
    computed: {
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      date(val) {
        this.getCenterAPI(val);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper-bg
    background url("../../assets/wrapper-bg.png") no-repeat center
    background-size 100% 100%
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)

    .w-seach
      position absolute
      top -80px
      right 113px
      z-index 1000
      box-sizing border-box
      padding-left 42px
      width 270px
      line-height 70px
      font-size 22px
      color #cbfbff
      background url('../../assets/search-bg2.png') no-repeat
      background-size 100% 100%
      cursor pointer

      p
        width 150px
        text-align center
        white-space nowrap
        overflow hidden
        text-overflow ellipsis

    .content
      width 100%
      height 100%
      display flex
      flex-direction row
      padding .2rem .3rem
      box-sizing border-box

      .w-center
        display flex
        flex 2
        flex-direction column
        box-sizing border-box
        .c-title
          color #fff
          font-size 21px
          font-weight 700
          line-height 28px
          text-shadow 0 0 10px #276bf4
          text-align center
          padding 0.27rem 0
        .w-echart
          display flex
          flex 1
          flex-direction row

</style>
